import { Injectable } from '@angular/core';
import { HttpService } from '../common/service/http.service';
import { PROCESS_TIME_REPORT, TIMEOUT_WARNING_API, TIMEOUT_TASK_API, TIMEOUT_FLOW_API, TASK_TIME_REPORT, PROCESS_TOTAL_REPORT, TASK_TOTAL_REPORT, EXCEPRION_END_REPORT, USER_DO_REPORT, PROCESS_REPORT_CONTEXT, TASK_REPORT_CONTEXT } from './report-query.constant';
import { RTS_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
// import { RTS_HTTP_URL } from '../common/constant/config';

@Injectable()
export class ReportQueryService {

  constructor(
    private httpService: HttpService
  ) {


  }

  // 查询超时预警
  listNoTimeOutTaskList = (params) => this.httpService.post(RTS_HTTP_URL + TIMEOUT_WARNING_API.listNoTimeOutTaskList, params, null);

  // 查询超时任务
  listTimeOutTaskList = (params) => this.httpService.post(RTS_HTTP_URL + TIMEOUT_TASK_API.listTimeOutTaskList, params, null);

  // 查询超时流程
  listTimeOutWorkFlowList = (params) => this.httpService.post(RTS_HTTP_URL + TIMEOUT_FLOW_API.listTimeOutWorkFlowList, params, null);

  // 查询流程处理时间报表
  processTimeReport = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_TIME_REPORT.processTimeReport, params, null);

  // 查询任务处理时间报表
  taskTimeReport = (params) => this.httpService.post(RTS_HTTP_URL + TASK_TIME_REPORT.taskTimeReport, params, null);

  // 查询流程统计报表
  tiemOutProcessReport = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_TOTAL_REPORT.tiemOutProcessReport, params, null);

  // 查询任务统计报表
  tiemOutTaskReport = (params) => this.httpService.post(RTS_HTTP_URL + TASK_TOTAL_REPORT.tiemOutTaskReport, params, null);

  // 查询异常结束报表
  exceptionEndReport = (params) => this.httpService.post(RTS_HTTP_URL + EXCEPRION_END_REPORT.exceptionEndReport, params, null);

  // 查询人员绩效报表
  userDoReport = (params) => this.httpService.post(RTS_HTTP_URL + USER_DO_REPORT.userDoReport, params, null);

  // 查询流程处理报表（员工）
  wfInstencePersonalReport = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_REPORT_CONTEXT.wfInstencePersonalReport, params, null);

  // 查询流程处理报表（机构）
  wfInstenceOrgReport = (params) => this.httpService.post(RTS_HTTP_URL + PROCESS_REPORT_CONTEXT.wfInstenceOrgReport, params, null);

  // 任务流程处理报表（明细）
  wfTaskflowPersonalReport = (params) => this.httpService.post(RTS_HTTP_URL + TASK_REPORT_CONTEXT.wfTaskflowPersonalReport, params, null);

  // 任务流程处理报表（机构）
  wfTaskflowOrgReport = (params) => this.httpService.post(RTS_HTTP_URL + TASK_REPORT_CONTEXT.wfTaskflowOrgReport, params, null);

}
