import { Component, OnInit, ViewChild } from '@angular/core';
import { CALENDAR_CN, paginatorUtils, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { TreeNode, ConfirmationService, MenuItem, DataTable } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, expandLastNode, handleResult, conversionExtToNgTree, handleResult2, findObject, formatDate, getLastMonthStartDate, getLastMonthEndDate } from '../../common/func/common-func';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { DomSanitizer } from "@angular/platform-browser";
import { ReportQueryService } from '../report-query.service';
import { TaskHandleReportModel } from '../report-query-model';

@Component({
  selector: 'app-task-report',
  templateUrl: './task-report.component.html',
  styleUrls: ['./task-report.component.scss'],

})
export class TaskReportComponent implements OnInit {

  // 查询表单
  taskHandleForm: FormGroup;
  msg = [];
  // 任务处理报表列表
  taskHandleReportList: TaskHandleReportModel[] = [];
  // 任务接受开始时间
  queryStartTimeStart: Date;
  // 任务接受起截止时间
  queryStartTimeEnd: Date;

  CALENDAR_CN = CALENDAR_CN;
  //   查询实例
  queryTaskHandleReport: TaskHandleReportModel = new TaskHandleReportModel();


  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private reportQueryService: ReportQueryService,
  ) {
    this.createValidators();
  }


  createValidators() {
    this.taskHandleForm = this.fb.group({
      'processName': [''],
      'userIdName': [''],
      'processStartName': ['', Validators.required],
      'processEndName': ['', Validators.required],
      'orgObjName': ['', Validators.required]
    });
  }

  ngOnInit() {
    this.queryStartTimeStart = getLastMonthStartDate();
    this.queryStartTimeEnd = getLastMonthEndDate();
  }



  query(): void {
    this.querProcessHandleReportList(this.queryStartTimeStart, this.queryStartTimeEnd, this.queryTaskHandleReport.processName, this.queryTaskHandleReport.userId, this.queryTaskHandleReport.orgObj);
  }

  // 查询重置
  reset(): void {
    this.queryStartTimeStart = getLastMonthStartDate();
    this.queryStartTimeEnd = getLastMonthEndDate();
    this.queryTaskHandleReport.processName = null;
    this.queryTaskHandleReport.userId = null;
  }

  // 查询流程处理报表
  querProcessHandleReportList(TimeBegin: any, TimeEnd: any, processName?: string, doActor?: string, orgObj?: string): void {
    const createTimeBegin = TimeBegin ? formatDate(TimeBegin, FULL_HORIZONTAL_DATE_FORMAT) : getLastMonthStartDate();;
    const createTimeEnd = TimeEnd ? formatDate(TimeEnd, FULL_HORIZONTAL_DATE_FORMAT) : getLastMonthEndDate();
    const bodyParams = {
      createTimeBegin,
      createTimeEnd,
      processName,
      doActor,
      orgObj
    };
    this.reportQueryService.wfTaskflowPersonalReport(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.taskHandleReportList = data.pageData[0].result;
          this.taskHandleReportList && this.taskHandleReportList.length && this.taskHandleReportList.map(item => {
            item.customUserName = item.userId ? item.userName ? `${item.userId} (${item.userName})` : `${item.userId}` : '';
            item.customOrgObj = item.orgId ? item.orgName ? `${item.orgId} (${item.orgName})` : `${item.orgId}` : '';
          });
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      });
  }



}
