import { Component, OnInit, ViewChild } from '@angular/core';
import { CALENDAR_CN, RULE_ENGINE_HTTP_URL, RULE_ENGINE_CONTEXT_PATH_RULE_EDITOR, PARAM_PACKAGE, CONSTANT_PACKAGE, VARIABLE_PACKAGE, RULE_PACKAGE, RESOURCE_PACKAGE, paginatorUtils, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { TreeNode, ConfirmationService, MenuItem, DataTable } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, expandLastNode, handleResult, conversionExtToNgTree, handleResult2, findObject, formatDate } from '../../common/func/common-func';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { DomSanitizer } from "@angular/platform-browser";
import { ReportQueryService } from '../report-query.service';
import { TaskTimeReportModel, ProcessTimeReportModel, PersonnelPerfomanceReportModel, TimeOutWarningModel } from '../report-query-model';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-personnel-performance-report',
  templateUrl: './personnel-performance-report.component.html',
  styleUrls: ['./personnel-performance-report.component.scss'],

})
export class PersonnelPerformanceReportComponent implements OnInit {

  msg = [];
  // 任务处理时间报表
  processHandleTimeReportList: PersonnelPerfomanceReportModel[] = [];

  CALENDAR_CN = CALENDAR_CN;
  // 查询起始时间
  queryStartTime: Date;
  // 查询截止时间
  queryEndTime: Date;
  // 当前系统登录ID
  loginSys: string;
  total: string;
  // 分页实例
  paginatorInstance: PersonnelPerfomanceReportModel = new PersonnelPerfomanceReportModel();

  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private reportQueryService: ReportQueryService,
    private commfunc:Commfunc
  ) {

  }

  ngOnInit() {
    this.loginSys = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
  }

  // 按照搜索条件查询
  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.queryProcessTimeReportList(this.queryStartTime, this.queryEndTime);
  }

  // 查询重置
  reset(): void {
    this.queryStartTime = null;
    this.queryEndTime = null;
  }

  // 查询任务处理时间报表
  queryProcessTimeReportList(startTime?: any, endTime?: any): void {
    const beginDate = startTime == null ? null : formatDate(startTime, FULL_HORIZONTAL_DATE_FORMAT);
    const endDate = endTime == null ? null : formatDate(endTime, FULL_HORIZONTAL_DATE_FORMAT);
    const bodyParams = {
      userID: this.loginSys,
      beginDate,
      endDate,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.reportQueryService.userDoReport(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.processHandleTimeReportList = data.pageData[0].result;
          this.total = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      });
  }




}
