import { Component, OnInit, ViewChild } from '@angular/core';
import { Pager, RULE_ENGINE_HTTP_URL, RULE_ENGINE_CONTEXT_PATH_RULE_EDITOR, PARAM_PACKAGE, CONSTANT_PACKAGE, VARIABLE_PACKAGE, RULE_PACKAGE, RESOURCE_PACKAGE, paginatorUtils, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { TreeNode, ConfirmationService, MenuItem, DataTable } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, expandLastNode, handleResult, conversionExtToNgTree, handleResult2, findObject, formatDate } from '../../common/func/common-func';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { DomSanitizer } from "@angular/platform-browser";
import { ReportQueryService } from '../report-query.service';
import { TimeoutProcessReportModel, TimeOutWarningModel } from '../report-query-model';

@Component({
  selector: 'app-timeout-process-report',
  templateUrl: './timeout-process-report.component.html',
  styleUrls: ['./timeout-process-report.component.scss'],

})
export class TimeoutProcessReportComponent implements OnInit {

  msg = [];
  // 任务处理时间报表
  timeoutProcessReportList: TimeoutProcessReportModel[] = [];
  // 查询任务处理时间报表
  queryTimeoutProcessReport = {
    bigClassNo: '',
    littleClassNo: '',
    littleClassName: '',
    processCode: ''
  };
  total: string;
  // 分页实例
  paginatorInstance: TimeoutProcessReportModel = new TimeoutProcessReportModel();

  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private reportQueryService: ReportQueryService,
  ) {

  }

  ngOnInit() {
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
  }

  // 按照搜索条件查询
  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.querTimeoutProcessReportList(this.queryTimeoutProcessReport.bigClassNo, this.queryTimeoutProcessReport.littleClassNo, this.queryTimeoutProcessReport.littleClassName, this.queryTimeoutProcessReport.processCode);
  }

  // 查询重置
  reset(): void {
    this.queryTimeoutProcessReport = {
      bigClassNo: '',
      littleClassNo: '',
      littleClassName: '',
      processCode: ''
    };
  }

  // 查询任务处理时间报表
  querTimeoutProcessReportList(itemType?: string, itemNode?: string, itemName?: string, processCode?: string): void {
    const bodyParams = {
      itemType,
      itemNode,
      itemName,
      processCode,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.reportQueryService.tiemOutProcessReport(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.timeoutProcessReportList = data.pageData[0].result;
          this.timeoutProcessReportList && this.timeoutProcessReportList.length && this.timeoutProcessReportList.map(item => {
            item.customSystemCode = item.processCode.split(":")[0];
          });
          this.total = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      });
  }




}
