import { Component, OnInit, ViewChild } from '@angular/core';
import { Pager, RULE_ENGINE_HTTP_URL, RULE_ENGINE_CONTEXT_PATH_RULE_EDITOR, PARAM_PACKAGE, CONSTANT_PACKAGE, VARIABLE_PACKAGE, RULE_PACKAGE, RESOURCE_PACKAGE, paginatorUtils, FULL_HORIZONTAL_DATE_FORMAT } from '../../common/constant/config';
import { TreeNode, ConfirmationService, MenuItem, DataTable } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, expandLastNode, handleResult, conversionExtToNgTree, handleResult2, findObject, formatDate } from '../../common/func/common-func';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { DomSanitizer } from "@angular/platform-browser";
import { ReportQueryService } from '../report-query.service';
import { TaskTimeReportModel, ProcessTimeReportModel, TimeOutWarningModel } from '../report-query-model';

@Component({
  selector: 'app-process-time-report',
  templateUrl: './process-time-report.component.html',
  styleUrls: ['./process-time-report.component.scss'],

})
export class ProcessTimeReportComponent implements OnInit {

  msg = [];
  // 任务处理时间报表
  processHandleTimeReportList: ProcessTimeReportModel[] = [];
  // 查询任务处理时间报表
  queryProcessHandleTimeReport = {
    bigClassNo: '',
    littleClassNo: '',
    littleClassName: '',
    processCode: ''
  };
  total: string;
  // 分页实例
  paginatorInstance: ProcessTimeReportModel = new ProcessTimeReportModel();


  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private reportQueryService: ReportQueryService,
  ) {

  }

  ngOnInit() {
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
  }

  // 按照搜索条件查询
  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.queryProcessTimeReportList(this.queryProcessHandleTimeReport.bigClassNo, this.queryProcessHandleTimeReport.littleClassNo, this.queryProcessHandleTimeReport.littleClassName, this.queryProcessHandleTimeReport.processCode);
  }

  // 查询重置
  reset(): void {
    this.queryProcessHandleTimeReport = {
      bigClassNo: '',
      littleClassNo: '',
      littleClassName: '',
      processCode: ''
    };
  }

  // 查询任务处理时间报表
  queryProcessTimeReportList(itemType?: string, itemNode?: string, itemName?: string, processCode?: string): void {
    const bodyParams = {
      itemType,
      itemNode,
      itemName,
      processCode,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.reportQueryService.processTimeReport(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.processHandleTimeReportList = data.pageData[0].result;
          this.processHandleTimeReportList && this.processHandleTimeReportList.length && this.processHandleTimeReportList.map(item => {
            item.customSystemCode = item.processCode.split(":")[0];
          });
          this.total = data.pageData[0].totalRows;
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      });
  }

}
