
// 超时预警类型
export class TimeOutWarningModel {
  id: number;	                  //自增主键
  taskId: string;	              // 当前待办任务ID
  taskCode: string;             //	当前任务节点编码
  taskName: string;             //	当前任务节点定义名称
  taskDefId: string;	          // 当前任务节点定义ID
  taskType: string;	            // 当前任务节点类型
  workType: number;	            // 任务类型
  taskRate: number;            	// 当前任务级别
  taskUrgent: number;	          // 当前任务紧急程度
  taskBussName: string;	        // 当前任务业务名称
  processCode: string;         	// 流程编码
  customSystemCode: string;     // 截取后的流程编码
  processName: string; 	        // 流程名称
  processId: string;	          // 当前流程实例ID
  wfDesc: string; 	            // 流程备注
  customWorkCreater: string; 	  // 自定义流程发起人
  workCreater: string;        	// 流程发起创始人ID
  workCreaterName: string;      // 流程发起创始人姓名
  systemCode: string;          	// 系统分类编码
  createDate: number;	        	// 任务创建日期
  startDate: number;		        // 开始日期时间
  updateDate: number;	        	// 更改日期时间
  endDate: number;	           	// 截止日期时间
  prviousActor: string;        	// 前一任务成员
  superTaskId: string;        	// 上一节点任务Id
  parentTaskId: string;       	// 父任务Id
  doTaskActor: string;       	  // 处理人
  customdoTaskActor: string;    // 自定义任务处理人
  taskActors: string;         	// 代办人
  taskActorsData: string;      	// 当前任务成员信息
  workState: string;           	// 当前任务处理状态
  checkInFlag: string;         	// 是否可以检出
  workDoFlag: string;          	// 任务处理类型
  workUsrService: string;      	// 任务处理URL地址 / 服务
  workUrlFlag: string;         	// 任务处理URL标志
  workInParam: string;          // 任务服务方法入口参数
  bigClassNo: string;           // 大类编码
  littleClassNo: string;        //  小类编码
  bigClassName: string;        //  大类名称
  littleClassName: string;       //  小类名称
  startRow: string;             // 起始行数（非第N页）
  rowNum: string;               // 每页显示多少条
}

// 超时流程类型
export class TimeOutFlowModel {
  estEndDate: number;
  processDesc: string;               // 流程实例简述
  updateTime: number;                // 更新时间
  customStartUserId: string;         // 自定义发起人
  startUserId: string;               // 流程启动用户ID
  startUserName: string;             // 发起人姓名
  joinUserIds: string;               // 流程审批人员
  createTime: number;                // 创建时间
  processId: number;                 // 流程实例ID
  customSystemCode: string;          // 自定义流程编码
  systemCode: string;                // 系统分类编码
  processCode: string;               // 流程编码
  workState: string;                 // 当前任务处理状态
  isTimeout: string;
  processInstDesc: string;           // 流程实例描述
  startTime: number;                 // 开始时间
  endTime: number;                   // 截止时间
  id: number;                         // ID
  bigClassNo: string;                // 大类编码
  littleClassNo: string;              //  小类编码
  bigClassName: string;              //  大类名称
  littleClassName: string;             //  小类名称
  proccessName: string;               // 流程名称
  endType: string;                    // 流程结束类型
  startRow: string;                   // 起始行数（非第N页）
  rowNum: string;                      // 每页显示多少条
}

