import { Component, OnInit } from '@angular/core';
import { Pager, RULE_ENGINE_HTTP_URL, RULE_ENGINE_CONTEXT_PATH_RULE_EDITOR, PARAM_PACKAGE, CONSTANT_PACKAGE, VARIABLE_PACKAGE, RULE_PACKAGE, RESOURCE_PACKAGE, paginatorUtils } from '../../common/constant/config';
import { TreeNode, ConfirmationService, MenuItem } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { handleError, expandLastNode, handleResult, conversionExtToNgTree, handleResult2, findObject } from '../../common/func/common-func';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { DictionaryTypeModel } from '../../process-definition/process-definition-model';
import { DomSanitizer } from "@angular/platform-browser";
import { TimeOutWarningModel } from '../report-query-model';
import { TASK_NODE_TYPES, WORK_HANDLE_STATES, TASK_TYPES, TASK_RATE, TASK_URGENT, TASK_HANDLE_TYPES } from '../../common/constant/code.value';
import { ReportQueryService } from '../report-query.service';

@Component({
  selector: 'app-timeout-warning-query',
  templateUrl: './timeout-warning-query.component.html',
  styleUrls: ['./timeout-warning-query.component.scss'],

})
export class TimeoutWarningQueryComponent implements OnInit {

  msg = [];
  // 查询超时预警信息实例
  queryTimeoutWarning: TimeOutWarningModel = new TimeOutWarningModel();
  // 超时预警信息列表
  timeoutWarningList: TimeOutWarningModel[] = [];
  // 分页实例
  paginatorInstance: TimeOutWarningModel = new TimeOutWarningModel();
  // 数据总量
  total: string;
  // 超时提前通知时间
  alarmTime: number;
  // 节点类型
  nodeTypes = TASK_NODE_TYPES;
  // 当前任务处理状态
  handleState = WORK_HANDLE_STATES;
  // 任务类型
  taskTypes = TASK_TYPES
  // 任务级别
  taskRates = TASK_RATE;
  // 任务紧急程度
  taskUrgents = TASK_URGENT;
  // 任务处理类型
  askHandleTypes = TASK_HANDLE_TYPES;
  // 节点类型
  businessNodeTypes = TASK_NODE_TYPES;
  // 当前任务处理状态
  processState = WORK_HANDLE_STATES;



  constructor(
    private fb: FormBuilder,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private reportQueryService: ReportQueryService,
  ) {

  }

  ngOnInit() {
    this.paginatorInstance.rowNum = paginatorUtils.rowNum;
    this.paginatorInstance.startRow = paginatorUtils.startRow;
  }

  // 按照搜索条件查询
  queryFirst(table) {
    table.first = 0;
    this.query(table);
  }


  query(table): void {
    table.first = table.first || 0;
    table.rows = table.rows || parseInt(paginatorUtils.rowNum, 10);
    // 每页显示的条数
    this.paginatorInstance.rowNum = table.rows + '';
    // 当前页
    this.paginatorInstance.startRow = (table.first / table.rows) + 1 + '';
    this.queryTimeoutWarningList(this.alarmTime, this.queryTimeoutWarning.bigClassNo, this.queryTimeoutWarning.littleClassNo, this.queryTimeoutWarning.littleClassName);
  }

  // 查询重置
  reset(): void {
    this.alarmTime = null;
    this.queryTimeoutWarning = new TimeOutWarningModel();
  }

  // 查询超时预警列表
  queryTimeoutWarningList(alarmTime?: number, itemType?: string, itemNode?: string, itemName?: string): void {
    const bodyParams = {
      alarmTime: alarmTime ? `${alarmTime}时` : null,
      itemType,
      itemNode,
      itemName,
      pageRows: parseInt(this.paginatorInstance.rowNum, 10),
      curPage: parseInt(this.paginatorInstance.startRow, 10)
    };
    this.reportQueryService.listNoTimeOutTaskList(bodyParams)
      .subscribe(
      data => {
        handleResult(data, this.messageService, () => {
          this.timeoutWarningList = data.pageData[0].result;
          this.total = data.pageData[0].totalRows;
          this.timeoutWarningList && this.timeoutWarningList.length && this.timeoutWarningList.map(item => {
            item.customSystemCode = item.processCode.split(":")[0];
            item.customWorkCreater = item.workCreater ? item.workCreaterName ? `${item.workCreater} (${item.workCreaterName})` : `${item.workCreater}` : '';
            item.customdoTaskActor = item.workState === '0' ? item.taskActors : item.doTaskActor;
          });
        }, true, { errorMsg: '查询失败' });
      },
      err => {
        handleError(err, this.messageService, () => {
        });
      });
  }




}
