import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ReportQueryComponent } from './report-query.component';
import { TimeoutWarningQueryComponent } from './timeout-warning-query/timeout-warning-query.component';
import { TimeoutTaskQueryComponent } from './timeout-task-query/timeout-task-query.component';
import { TimeoutFLowQueryComponent } from './timeout-flow-query/timeout-flow-query.component';
import { TaskTimeReportComponent } from './task-time-report/task-time-report.component';
import { ProcessTimeReportComponent } from './process-time-report/process-time-report.component';
import { TimeoutTaskReportComponent } from './timeout-task-report/timeout-task-report.component';
import { TimeoutProcessReportComponent } from './timeout-process-report/timeout-process-report.component';
import { ExceptionEndReportComponent } from './exception-end-report/exception-end-report.component';
import { PersonnelPerformanceReportComponent } from './personnel-performance-report/personnel-performance-report.component';
import { ProcessReportComponent } from './process-report/process-report.component';
import { TaskReportComponent } from './task-report/task-report.component';
import { ProcessOrgReportComponent } from './process-org-report/process-org-report.component';
import { TaskOrgReportComponent } from './task-org-report/task-org-report.component';

export const routes: Routes = [
  {
    path: '',
    component: ReportQueryComponent,
    children: [
      { path: '', redirectTo: 'timeout-warning-query', pathMatch: 'full' },
      { path: 'timeout-warning-query', component: TimeoutWarningQueryComponent },
      { path: 'timeout-task-query', component: TimeoutTaskQueryComponent },
      { path: 'timeout-flow-query', component: TimeoutFLowQueryComponent },
      { path: 'process-report', component: ProcessReportComponent },
      { path: 'task-report', component: TaskReportComponent },
      { path: 'process-org-report', component: ProcessOrgReportComponent },
      { path: 'task-org-report', component: TaskOrgReportComponent },

      // { path: 'task-time-report', component: TaskTimeReportComponent },
      // { path: 'process-time-report', component: ProcessTimeReportComponent },
      // { path: 'personnel-performance-report', component: PersonnelPerformanceReportComponent },
      // { path: 'timeout-task-report', component: TimeoutTaskReportComponent },
      // { path: 'timeout-process-report', component: TimeoutProcessReportComponent },
      // { path: 'exception-end-report', component: ExceptionEndReportComponent },
    ]
  }
];

export const ReportQueryRouting: ModuleWithProviders = RouterModule.forChild(routes);
